import React, { useState } from "react";
import { Modal, Image } from "react-bootstrap";
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import "./ImageModal.css";

const ImageModal = ({ image }) => {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Image id="image-thumbnail" src={image.path} onClick={handleShow} />
      <Modal
        show={show}
        onHide={handleClose}
        animation={false}
        dialogClassName="image-modal-dialog"
        contentClassName="image-modal-content"
        aria-labelledby="image-modal-title"
        centered
      >
        <TransformWrapper
          wheel={{ step: 100 }}
          doubleClick={{ mode: "reset" }}
          options={{ centerContent: false }}
        >
          <TransformComponent>
            <div className="image-modal-container">
              <Modal.Header closeButton bsPrefix="image-modal-header">
                <Modal.Title id="image-modal-title" as="span">
                  {image.title}
                </Modal.Title>
              </Modal.Header>
              <Modal.Body bsPrefix="image-modal-body">
                <Image src={image.path} fluid />
              </Modal.Body>
              <Modal.Footer bsPrefix="image-modal-footer">
                <span>
                  <a href={image.path} download>
                    {"下载原图"}
                  </a>
                  &nbsp;&nbsp;&nbsp;&nbsp;
                  {"作者："}
                  {image.link ? (
                    <a href={image.link} target="_blank">
                      {image.author}
                    </a>
                  ) : (
                    <span>{image.author}</span>
                  )}
                </span>
              </Modal.Footer>
            </div>
          </TransformComponent>
        </TransformWrapper>
      </Modal>
    </>
  );
};
export default ImageModal;
